.vongquay-box{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.vongquay-box .box{position:relative;border-radius:10px;margin-top:10px;margin-left:30px;padding:10px;width:130vh;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;text-align:center;text-shadow:2px 2px 10px #fff;background-color:#000}.vongquay-box .box .box-new{position:absolute;border-radius:10px;width:130vh;text-align:center;text-shadow:2px 2px 10px #fff}.vongquay-box .box .btn-vongquay{z-index:10000;position:relative;top:-2vh;width:20vh;border-radius:10px;outline:none;padding:20px;background-color:#8a2be2;color:#fff;border:none}