.vehicle{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.vehicle .box{border-radius:10px;margin-top:10px;margin-left:30px;width:38vh;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#111;padding:10px}.vehicle .box .img{width:100%;height:4vh}.vehicle .box .img img{border-radius:10px;width:100%}.vehicle .box .name-car{position:relative;top:180px;font-size:20px;text-align:center}.vehicle .box .name-type{position:relative;top:200px;font-size:12px;color:gray;text-align:start}.vehicle .box .name-type span{margin-left:20px;color:#fff;font-size:18px}.vehicle .box .name-number{text-align:end;top:22px;position:relative}.vehicle .box .name-number span{margin-left:10px;padding:8px;background-color:#ec1b62;border-radius:10px}.vehicle .box .btn{margin-top:210px}