.skill{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.skill .box{border-radius:10px;margin-top:10px;margin-left:30px;width:38vh;height:33vh;background-color:#111;padding:10px}.skill .box p{text-align:center;top:2vh;position:relative;margin-top:10px;font-size:20px;color:gray}.skill .box img{display:inline-block;position:relative;width:100%;border-radius:10px}.skill .box span{position:relative;width:16vh;top:5vh;font-size:18px;color:#fff}.skill .box span h3{position:absolute;top:-20px;right:10px;color:gray;font-size:15px}