.hmenu__gunshop__navpanel {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 20%;
    margin-right: 2%;
    background: rgba(0, 0, 0, 0.5);
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    border-radius: 20px;
}
.tatoo__content__img__container img{
    width: 100%;
}
.hmenu__gunshop__navpanel__exitbtn {

    font-family: Montserrat;
    text-align: center;
    font-size: 2rem;
    letter-spacing: 0.1rem;
    cursor: pointer;
    padding: 20px;

}

.hmenu__gunshop__navpanel__item {
    margin-left: 1.5rem !important;
    margin-right: 1.5rem !important;
}

.hmenu__gunshop__navpanel__list {
    display: flex;
    flex-direction: column;
    margin-bottom: auto;
    overflow-x: auto;
}

.hmenu__gunshop__navpanel__list__item {
    cursor: pointer;
    padding-top: 5%;
    padding-bottom: 5%;
    margin: 1rem 1.5rem 1rem 1.5rem;
    width: 90%;
    text-align: center;
    font-family: 'Gotham Pro Regular';
    font-size: 1.3rem;
    letter-spacing: 0.05rem;
    border: 0.5px solid rgba(255, 255, 255, 0.15);
    border-radius: 7px;
}

.hmenu__gunshop__navpanel__list__item_active {
    cursor: pointer;
    padding-top: 5%;
    padding-bottom: 5%;
    background-color: brown;
    width: 100%;
    text-align: center;
    font-family: 'RobotoBold';
    font-size: 1.3rem;
    letter-spacing: 0.05rem;
}
.tatoo__content__exitbtn__container{
    padding: 20px;
    background-color: black;
    margin-top: 20px;
    text-align: center;
}
.hmenu__gunshop__navpanel__banner {
    height: 15%;
    padding: 20%;
    width: auto;
}
.hmenu__gunshop__catalog {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: -webkit-min-content;
    grid-template-rows: min-content;
    grid-column-gap: 2rem;
    grid-row-gap: 2rem;
    height: 100%;
    width: 100%;
    padding-right: 6%;
    overflow-y: auto;
}

.hmenu__gunshop__catalog.cols-3 {
    grid-template-columns: 33% 33% 33%;
}

.hmenu__gunshop__catalog__container {
    overflow-y: auto;
    padding-right: 1%;
    display: flex;
    background: rgba(0, 0, 0, 0.5);
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    margin-right: 2%;
    padding: 1.5%;
    width: 40%;
}

.hmenu__gunshop__catalog__item {
    border-radius: 10px;
    display: flex;
    padding: 1.5rem;
    flex-direction: column;
    cursor: pointer;
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.hmenu__gunshop__catalog__item_active {
    background: rgba(0, 0, 0, 0.5);
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    box-sizing: border-box;
    border-radius: 10px;
    display: flex;
    padding: 1.5rem;
    flex-direction: column;
    cursor: pointer;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.hmenu__gunshop__catalog__item__img {
    width: 55%;
    object-fit: contain;
}

.hmenu__gunshop__catalog__item__img__container {
    margin-top: 10%;
    margin-bottom: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hmenu__gunshop__catalog__item__name {
    font-family: 'Gotham Pro Medium';
    font-size: 2rem;
}

.hmenu__gunshop__catalog::-webkit-scrollbar {
   width: 0.4rem;
 }
   
 .hmenu__gunshop__catalog::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.35);
    border-radius: 15px;
 }
   
 .hmenu__gunshop__catalog::-webkit-scrollbar-thumb {
    background: #FFFFFF;
    border-radius: 15px;
}

.hmenu__gunshop__catalog__item__info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.hmenu__gunshop__catalog__item__calibr {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.hmenu__gunshop__catalog__item__price {
    font-family: 'Gotham Pro Medium';
    font-size: 2.2rem;
}

.hmenu__gunshop__catalog__item__calibr__text {
    font-family: 'Gotham Pro Regular';
    font-size: 1.1rem;
    letter-spacing: 0.05rem;
}

.hmenu__gunshop__catalog__item__calibr__name {
    font-family: 'Gotham Pro Medium';
    font-size: 1.3rem;
}

.hmenu__gunshop__catalog__item__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: visible;
    height: 24px;
}

.hmenu__gunshop__catalog__item__sale {

    position: relative;
    padding: 6px;
    /*right: 50%;*/
    bottom: 34px;

    /*padding-top: 3%;
    padding-bottom: 3%;
    padding-left: 1.3rem;
    padding-right: 1.3rem;*/
    border-radius: 0.4rem;
    font-family: 'Gotham Pro Regular';
    background: linear-gradient(105.63deg, rgba(255, 64, 57, 0) -80.94%, rgba(255, 64, 57, 0.45) 275.52%);
    border: 0.5px solid #FF4039;
    border-radius: 5px;
}
.hmenu__gunshop__stats {
    display: flex;
    flex-direction: column;
    padding-left: 2%;
    padding-right: 2%;
    padding-top: 1.5%;
    padding-bottom: 1.5%;
    width: 22%;
    background: rgba(0, 0, 0, 0.5);
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    border-radius: 20px;
}

.hmenu__gunshop__stats__name {
    font-family: 'Gotham Pro Medium';
    font-size: 1.4rem;
    text-align: center;
    letter-spacing: 0.05rem;
}

.hmenu__gunshop__stats__pname {
    font-family: 'Gotham Pro Regular';
    font-size: 1.5rem;
    letter-spacing: 0.05rem;
}

.hmenu__gunshop__stats__params {
    display: flex;
    flex-direction: column;
    margin-top: 8%;
    margin-bottom: auto;
}

.hmenu__gunshop__stats__params__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 2rem;
    margin-top: 2%;
    margin-bottom: 2%;
}

.hmenu__gunshop__stats__params__name {
    z-index: 10;
    font-family: 'RobotoBold';
    font-size: 1.2rem;
    padding-left: 5%;
}

.hmenu__gunshop__stats__params__percent {
    z-index: 10;
    font-family: 'RobotoBold';
    font-size: 1.2rem;
}

.hmenu__gunshop__stats__flatbtn {
    width: 100%;
    margin-top: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
    font-family: 'Gotham Pro Regular';
    font-size: 1rem;
    border: 0.5px solid rgba(255, 255, 255, 0.25);
    border-radius: 10px;
    letter-spacing: 0.05rem;
    margin-bottom: 2%;
    cursor: pointer;
}

.hmenu__gunshop__stats__price {
    display: flex;
    flex-direction: column;
}

.hmenu__gunshop__stats__price__text {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 5%;
}

.hmenu__gunshop__hide {
    display: none !important;
}

.hmenu__gunshop__stats__price__gtext {
    font-family: 'Gotham Pro Medium';
    font-size: 1.2rem;
}

.hmenu__gunshop__stats__price__gprice {
    font-family: 'Gotham Pro Medium';
    font-size: 2rem;
}

.hmenu__gunshop__stats__about {
    margin-top: 5%;
    font-family: 'Gotham Pro Regular';
    font-size: 1.2rem;
    letter-spacing: 0.05rem;
}
