body{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.btn-exit {
    text-align: center;
}
.btn-exit .fa-xmark{
    color:white;    
 
}
.cofferTaxIntermediate{
    font-size: 17px;
}
.container-menu{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.box-menu{
    width: 150vh;
    height: 70vh;
    background-color: #1d2124;
}
.box-menu .header{
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
    width: 100%;
    background-color: #191a1e;
    height: 7vh;
    grid-column-gap: 10px;
}
.box-menu .header .logo{
    font-size: 20px;
    position: relative;
    left: 80px;
}
.box-menu .header .job{
    position: relative;
    line-height: 50px;
    text-align: center;
    font-size: 13px;
    width: 100%;
    height: 5vh;
    background-color: #303135;
}
.box-menu .header .license{
    position: relative;
    line-height: 50px;
    text-align: center;
    font-size: 13px;
    width: 100%;
    height: 5vh;
    background-color: #303135;
}
.box-menu .header .tax{
    position: relative;
    line-height: 50px;
    text-align: center;
    font-size: 13px;
    width: 100%;
    height: 5vh;
    background-color: #303135;
}
.box-menu .header .job:hover{
    background-color: white;
    color :#191a1e;
}
.box-menu .header .license:hover{
    background-color: white;
    color :#191a1e;
}
.box-menu .header .tax:hover{
    background-color: white;
    color :#191a1e;
}
.body .content .img img{
    position: relative;
    width: 400px;
    left: 10px;
    top: 50px;
}

.content{
    display: flex;
    justify-content: center;
    align-items: center;
}
.title-body{
    position: relative;
    left: 10vh;
    width: 30vh;
}
.title-body h2{
    font-size: 30px;
    font-weight: 900;
}
.title-body span{
    font-size: 20px;
}
.title-body span p{
    color: red;
    font-weight: 900;
    font-size: 30px;
}
.btn-select{
    margin-top: 90px;
    display: grid;
    grid-template-rows: 30% 30%;
    grid-row-gap:50px ;
}
.btn-res{
    width: 40vh;
}
.btn-res button{
    width: 30vh;
    height: 10vh;
    background-color: #292a2e;
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
}
.btn-res button:hover{
    background-color: white;
    color:black;
}
.btn-family{
    width: 40vh;
}
.btn-family button{
    width: 30vh;
    height: 10vh;
    background-color: #292a2e;
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
}
.btn-family button:hover{
    background-color: white;
    color:black;
}
/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
.box-job{  
    width: 150vh;
    height: 60vh;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-row-gap:10px ;
    overflow-y: scroll;

}
.box-item{
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #3f4142;
    display: flex;
    border-bottom: 1px solid gray;
    border-top: 1px solid gray;

}

.box-item .img-job{
    width: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 1px solid gray;
}
.text{
    padding-left: 20px;
    padding-right: 20px;
    text-align: left;
    width: 78vh;
}
.text .title-job{
    font-size: 20px;
    color:#b5b5b8 ;
}
.text .description{
    font-size: 15px;

}

.btn-getjob{
    text-align: center;
    width: 30vh;
}
.btn-getjob button{
    width: 20vh;
    height: 6vh;
    background-color: #b5b5b8;
    color: black;
}
.btn-getjob button:hover{
    background-color: purple;
    color:white;
}
.box-item .img-job img{
    border-radius: 50px;
    width: 250px;
}
.title-job span{
    padding: 1px;
    border: 1px solid gray;
    font-size: 15px;
    margin-left: 20px;
    color:red;
}


