.carrent__container {
    position: absolute;
    width: 100%;
    font-family: ProximaNova;
    height: 100%;
    left: 0;
    top: 0;
    -webkit-animation: fadeInFromNone 0.5s ease-out;
    animation: fadeInFromNone 0.5s ease-out;
    display: flex;
    justify-content: center;
    align-items: center;
}

.carrent__content {
    background: rgba(107, 58, 58,1);
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    display: flex;
    flex-direction: column;
    height: 80%;
    padding-right: 3%;
    background-image: url(https://img.tinbanxe.vn/webp/images/Mercedes/Mercedes-GLS-500/mercedes-gls-500-2020.png);
    background-repeat: no-repeat;
}

.tatoo__content__header__img {
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    border-radius: 10px;
}

.tatoo__content__img__container {
    padding: 1.5rem;
}

.carrent__content__list {
    display: grid;
    grid-template-columns: 30% 30% 30%;
    grid-column-gap: 4rem;
    grid-row-gap: 2rem;
    height: 100%;
    padding: 0 0 2% 2%;
}

.carrent__content__list__container {
    height: 80%;
    overflow-y: auto;
}

.carrent__content__list__item__info__column {
    display: flex;
    flex-direction: column;
}


.carrent__content__list__container::-webkit-scrollbar {
    width: 0.8rem;
  }
    
  .carrent__content__list__container::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.35);
    border-radius: 15px;
  }
    
  .carrent__content__list__container::-webkit-scrollbar-thumb {
    background: #FFFFFF;
    border-radius: 15px;
 }
.carrent__content__header {
    display: flex;
    width: 98%;
    height: 14%;
    overflow: hidden;
    align-items: center;
    padding-left: 2%;
}

.carrent__content__header__banner {
    width: auto;
    height: 100%;
}

.carrent__content__header__title {
    font-family: ProximaNova;
    font-size: 1.6rem;
    letter-spacing: 0.05rem;
    margin-right: auto;
}

.carrent__content__header__exitbtn {
    font-family: ProximaNova;
    font-size: 1.2rem;
    letter-spacing: 0.05rem;
    margin-left: 5%;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background: rgba(0, 0, 0, 0.2);
    border: 0.5px solid rgba(255, 255, 255, 0.25);
    border-radius: 7px;
}
.carrent__content__list__img {
    width: 70%;
    padding-left: 10%;
    padding-right: 10%;
    height: auto;
}

.carrent__content__list__img__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: top;
}

.carrent__content__list__img__item {
    width: 100%;
    height: auto;
    border-radius: 7px;
}

.carrent__content__list__item {
    display: flex;
    flex-direction: column;
}


.carrent__content__list__item__info__price {
