.shop-box{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.shop-box .box{background-size:cover;position:relative;border-radius:10px;margin-top:10px;margin-left:30px;width:38vh;background-color:#111;padding:10px;text-align:center;text-shadow:2px 2px 10px #fff}.shop-box .box p{position:relative;top:1vh}.shop-box .box .img img{width:90%}.shop-box .box:hover{background-color:#fff;text-shadow:2px 2px 10px #fff}