.accountmenu__content__cards__blitem {
    display: flex;
    height: 90%;
    width: 150vh;
    background: rgba(163, 62, 62, 0.35);
    flex-direction: column;
}

.accountmenu__content__cards__settings__container {
    display: flex;
    flex-direction: column;
    padding-left: 3.5rem;
    padding-right: 3.5rem;
    margin-top: 5%;
}

.accountmenu__cards__question__container {
    padding-left: 3.5rem;
    padding-right: 3.5rem;
}

.accountmenu__content__cards__settings__header__type {
    font-family: ProximaNova;
    color: #fff;
    font-size: 15px;
    margin-bottom: 2%;
}

.accountmenu__content__cards__settings__header__name {
    font-family: ProximaNova;
    color: #fff;
    font-size: 23px;
    margin-bottom: 3rem;
}

.accountmenu__content__cards__settings__list {
    display: flex;
    flex-direction: column;
}

.accountmenu__content__cards__settings__list__item {
    font-family: ProximaNova;
    color: #fff;
    margin-top: 0.6rem;
    margin-bottom: 0.6rem;
    padding-left: 1.4rem;
    padding-right: 1.4rem;
    padding-top: 1rem;
    font-size: 14px;
    padding-bottom: 1rem;
    box-sizing: border-box;
    cursor: pointer;
}

.accountmenu__content__cards__settings__list__item_selected {
    font-family: ProximaNova;
    color: #fff;
    margin-top: 0.6rem;
    margin-bottom: 0.6rem;
    padding-left: 1.4rem;
    padding-right: 1.4rem;
    padding-top: 1rem;
    font-size: 1.2rem;
    padding-bottom: 1rem;
    background: rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    cursor: pointer;
}

.accountmenu__content__cards__settings__bcontainer__name {
    font-family: ProximaNova;
    font-size: 1.6rem;
}

.accountmenu__content__cards__settings__bcontainer__desc {
    font-family: ProximaNova;
    font-size: 1.1rem;
    color: #fff;
    margin-top: 10px;
}

.accountmenu__content__cards__setting__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 2%;
    margin-bottom: 2%;
}

.accountmenu__content__cards__setting__checkbox__btn:first-child {
    margin-right: 1rem;
}

.accountmenu__content__cards__setting__checkbox__btn_off:first-child {
    margin-right: 1rem;
}

.accountmenu__content__cards__setting__checkbox__btn_on:first-child {
    margin-right: 1rem;
}

.accountmenu__content__cards__setting__checkbox__btn {
    box-sizing: border-box;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-family: ProximaNova;
    font-size: 1.2rem;
    cursor: pointer;
}

.accountmenu__content__cards__setting__checkbox__btn_on {
    background: linear-gradient(95.53deg, #00D277 -207.08%, rgba(1, 214, 122, 0) 122.75%);
    border: 0.5px solid #00D879;
    box-sizing: border-box;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-right: 8%;
    font-family: ProximaNova;
    font-size: 1.2rem;
    cursor: pointer;
}

.accountmenu__content__cards__setting__checkbox__btn_off {
    background: linear-gradient(95.53deg, #FF6969 -207.08%, #FF6969 -207.05%, rgba(255, 112, 112, 0) 122.75%);
    border: 0.5px solid #FF9191;
    box-sizing: border-box;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-family: ProximaNova;
    font-size: 1.2rem;
    cursor: pointer;
}

.accountmenu__content__cards__setting__name {
    font-family: ProximaNova;
    font-size: 1.2rem;
}
.accountmenu__content__cards__setting__desc {
    font-family: ProximaNova;
    font-size: 1.0rem;
    letter-spacing: 0.12rem;
}

.accountmenu__content__cards__setting__listmenu__name {
    font-family: ProximaNova;
    font-size: 1.2rem;
    padding: 0 0.4rem 0 0.4rem;
}

.accountmenu__content__cards__setting__listmenu {
    display: flex;
}
.accountmenu__scrollable {
    overflow-y: auto;
    width: 70vh;
    margin-left: 20px;
    background-color:#111 ;
    color: gray;
    border-radius: 10px;
}

.accountmenu__scrollable::-webkit-scrollbar {
   width: 0.8rem;
 }
   
 .accountmenu__scrollable::-webkit-scrollbar-track {
   background: #303030;
 }
   
 .accountmenu__scrollable::-webkit-scrollbar-thumb {
   background: #fff;
}
