.report{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.report .box{border-radius:10px;margin-top:10px;margin-left:30px;width:38vh;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#111;padding:20px}.report .box p{margin-top:20px;position:relative;top:10px;font-size:12px;color:gray;text-align:start}.report .box p span{margin-left:20px;color:#fff;font-size:18px}