.container-auth{background-image:url(https://majestic-web-thanhhaicute.vercel.app/img/background.webp);position:absolute;width:100%;font-family:ProximaNova;height:100%;left:0;top:0;-webkit-animation:fadeInFromNone .5s ease-out;animation:fadeInFromNone .5s ease-out;display:flex;justify-content:center;align-items:center}.container-auth .resigter{position:absolute;width:120vh;height:74vh;background-color:#161b1f;overflow:hidden;display:grid;grid-template-columns:1fr 1fr}.container-auth .resigter img{width:60vh}.container-auth .resigter .right{top:40px;position:relative;text-align:center}.container-auth .resigter .right .account{margin-top:20px}.container-auth .resigter .right .account input{border:none;outline:none;font-size:17px;width:80%;padding:15px;text-align:start;background-color:#101317;color:#fff}.container-auth .resigter .right .email{margin-top:10px}.container-auth .resigter .right .email input{border:none;outline:none;font-size:17px;width:80%;padding:15px;text-align:start;background-color:#101317;color:#fff}.container-auth .resigter .right .password{margin-top:10px}.container-auth .resigter .right .password input{border:none;outline:none;font-size:17px;width:80%;padding:15px;text-align:start;background-color:#101317;color:#fff}.container-auth .resigter .right .rule{top:20px;position:relative;left:48px;text-align:start}.container-auth .resigter .right .rule p{margin-top:10px}.container-auth .resigter .right .rule p span{font-size:22px;color:gray}.container-auth .resigter .right .btn-resigter{position:absolute;bottom:6vh;left:-2vh;width:100%}.container-auth .resigter .right .btn-resigter button{margin-left:20px;position:relative;bottom:0;left:-1vh;width:45%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:20px;background-color:#23282b;border:none;color:#fff;font-size:20px;font-weight:bold;cursor:pointer}.container-auth .resigter .right .btn-resigter button:hover{background-color:#45091d;color:#fff}