.house{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.house .box{border-radius:10px;margin-top:10px;margin-left:30px;width:38vh;height:60vh;background-color:#111;padding:10px}.house .box p{margin-top:20px;position:relative;top:10px;font-size:12px;color:gray;text-align:start}.house .box p span{margin-left:20px;color:#fff;font-size:18px}.house .box .img img{display:inline-block;width:100%}