
@font-face {
    font-family: ProximaNova;
    src: url('../css/fonts/ProximaNova-Bold.woff');
}
@font-face {
    font-family: ProximaNova;
    src: url('../css/fonts/ProximaNova-Extrabld.woff');
}
@font-face {
    font-family: ProximaNova;
    src: url('../css/fonts/ProximaNova-Light.woff');
}
@font-face {
    font-family: ProximaNova;
    src: url('../css/fonts//ProximaNova-Regular.woff');
}
@font-face {
    font-family: ProximaNova;
    src: url('../css/fonts/ProximaNova-Semibold.woff');
}
body {
    font-family: ProximaNova;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    overflow: hidden;
    margin: 0;
  }
.position-modal  {
    position: absolute;
    width: 100%;
    font-family: ProximaNova;
    height: 100%;
    left: 0;
    top: 0;
    -webkit-animation: fadeInFromNone 0.5s ease-out;
    -moz-animation: fadeInFromNone 0.5s ease-out;
    -o-animation: fadeInFromNone 0.5s ease-out;
    animation: fadeInFromNone 0.5s ease-out;
    display: flex;
    justify-content: center;
    align-items: center;
}
.main-input-modal {
    width: 70vh;
    height: 20vh;
    background-color: #18191d;
    border-radius: 1px;
    position: relative;
    display: flex;
    padding: 1.5rem;
    flex-direction: column;
    align-items: center;
}

.modal-box-m {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.linear-input-modal-top1 {
    width: 170px;
    height: 6px;
    background: #fff;
    position: absolute;
    right: 0px;
    top: -4px;
}

.linear-input-modal-top2 {
    width: 15px;
    height: 6px;
    background: #fff;
    position: absolute;
    right: 174px;
    top: -4px;
}

.modal-b-title {
    position: absolute;
    top: 20px;
    font-family:ProximaNova;
    font-size: 1rem;
    color: #FFFFFF;
}

.modal-b-input {
    position: absolute;
    top: 7vh;
    width: 80%;
    max-height: 795px;
    min-height: 60px;
    padding: 1rem;
    height: 40px;
    min-width: 540px;
    box-sizing: border-box;
    font-family: ProximaNova;
    font-size: 14px;
    color: #ffffff;
    background-color: #1d1e21;
    outline: none;
    box-sizing: border-box;
}

.modal-box-input {
    width: 579px;
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;
}

.modal-btn {
    text-align: center;
    bottom: 20px;
    position: absolute;
    font-family: ProximaNova;
    font-size: 1rem;
    color: white;
    background-color: #000;
    margin-left: 1rem;
    box-sizing: border-box;
    border-radius: 2px;
    padding: 20px;
    width: 70%;
}

.modal-btn:hover {
    background: #fff;
    color: #000;
}

.input-meta-text {
    width: 540px;
    display: flex;
    margin-left: 14px;
    justify-content: flex-end;
    font-family: ProximaNova;
    margin-bottom: 7px;
    font-size: 13px;
}