.container-box{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.container-box .box{position:relative;border-radius:10px;margin-top:10px;margin-left:30px;width:38vh;background-color:#111;padding:10px;text-align:center;text-shadow:2px 2px 10px #fff}.container-box .box .img img{width:100%}.container-box .box:hover{background-color:#fff;color:#000}