.character{width:80%;display:grid;grid-template-rows:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;padding-top:20px;padding-bottom:40px;position:relative;margin-left:20px;margin-right:20px;bottom:20px}.character .box{border-radius:10px;margin-top:10px;margin-left:30px;width:40vh;height:20vh;background-color:#111}.character .box p{top:2vh;position:relative;left:5vh;margin-top:10px;font-size:12px;color:gray}.character .box p span{position:relative;left:10px;font-size:18px;color:#fff}