/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
.box-job{  
    width: 150vh;
    height: 60vh;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-row-gap:10px ;
    overflow-y: scroll;

}
.box-item{
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: fit-content;
    background-color: #3f4142;
    display: flex;
    border-bottom: 1px solid gray;
    border-top: 1px solid gray;

}

.box-item .img-job{
    width: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 1px solid gray;
}
.text{
    padding-left: 20px;
    padding-right: 20px;
    text-align: left;
    width: 78vh;
}
.text .title-job{
    font-size: 20px;
    color:#b5b5b8 ;
}
.text .description{
    font-size: 15px;

}

.btn-getjob{
    text-align: center;
    width: 30vh;
}
.btn-getjob button{
    width: 20vh;
    height: 6vh;
    background-color: #b5b5b8;
    color: black;
}
.btn-getjob button:hover{
    background-color: purple;
    color:white;
}
.box-item .img-job img{
    border-radius: 50px;
    width: 250px;
}
.title-job span{
    padding: 1px;
    border: 1px solid gray;
    font-size: 15px;
    margin-left: 20px;
    color:red;
}